/**
 * Vue Router support
 */import{getCurrentInstance}from'vue';export const routeProps={to:[String,Object],url:String,replace:Boolean};export function route({to,url,replace,$router:router}){if(to&&router){router[replace?'replace':'push'](to);}else if(url){replace?location.replace(url):location.href=url;}}export function useRoute(){const vm=getCurrentInstance().proxy;return()=>route(vm);}